 <template>
  <div class="block">
    <span class="demonstration">
      <el-table :data="thisPage" style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="questionId" label="ID" width="50"></el-table-column>
        <el-table-column prop="questionDetail" label="题目" width="300"></el-table-column>
        <el-table-column prop="questionAddTime" label="添加时间" width="180"></el-table-column>
        <el-table-column prop="questionUpdateTime" label="最近更新时间"></el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click="handleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </span>
    <div style="position:absolute;bottom:20px;left:45%">
      <el-pagination
        :hide-on-single-page="true"
        layout="prev, pager, next"
        @current-change="getQuestionList"
        :page-count="this.question.length"
      ></el-pagination>
    </div>
    <!-- <el-dialog title="编辑问题" :visible.sync="isEdit">
      <el-form :model="form">
        <el-form-item label="活动名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="活动区域" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isEdit = false">取 消</el-button>
        <el-button type="primary" @click="isEdit = false">确 定</el-button>
      </div>
    </el-dialog> -->
  </div>
</template>

  <script>
export default {
  inject: ["reload"],
  data() {
    return {
      question: [],
      thisPage: [],
      isEdit: false
    };
  },
  mounted() {
    this.$api.getAllQuestion().then(data => {
      var questionData = data.data;
      var chunk = 10;
      for (var i = 0, j = questionData.length; i < j; i += chunk) {
        this.question.push(questionData.slice(i, i + chunk));
      }
      this.thisPage = this.question[0];
    });
  },
  methods: {
    handleClick: function(a) {
      console.log(a);
    },
    getQuestionList(pageNum) {
      this.thisPage = this.question[--pageNum];
    },
    edit(question){

    }
  }
};
</script>


